body{
  margin: 0;
  background-color: black;
  box-sizing: border-box;
  font-family: 'Courier New', Courier, monospace;
}

.main-container{
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100vw;
  
}

.heading-container{
  height: 20vh;
  width: 100vw;
  display: flex;
  align-self: center;
  align-items: center;
}

.heading-container h2{
  color: black;
  font-size: 40px;
}
.pokemon-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  /* border: 1px solid black; */
}
.pokemon-container:hover{
  /* transform: scale(1.2); */
}
.card-container{
  width: 200px;
  background-color: red;
  transition: transform 1s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem 4px;
  margin: 1rem;
  border-radius: 1px solid lightgray;
  text-align: center;
  box-shadow: 5px 10px rgba(0, 0, 0, 0.4);
}

.card-container:hover{
  transform: scale(1.2);
}

.card-container img{
  width: 100px;
  height: 100px;
}

.grass {
  background-color: green;
}
.fire{
  background: linear-gradient(
    90deg,
    rgba(131,50,100,1) 0%,
    rgba(253,29,29,1) 50%,
    rgba(252,176,69,1) 100%
  );
}
.water{
  background-color: aqua;
}

.bug{
  background-color:rgb(189, 200, 235);
}

.button {
  margin-bottom: 20px;
  height: 20px;
  width: 100px;
}

.expanded-overlay{
  border: 1px solid black;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: white;
  z-index: 100;
}
.expanded-left{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.expanded-image{
  margin-bottom: 0.5rem;
  width: 200px;
}

.expanded-name{
  font-size: 2rem;

}

.expanded-right{
  margin-left: 2rem;
  margin-top: 2.5rem;
}
.close-button{
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  border: 1px solid black;
  cursor: pointer;
  
}
.expanded-table{
  width: 100%;
}
.expanded-table td{
  padding: 0.5rem;

}
.expanded-table tr td:nth-child(1){
  font-weight: bold;
}

/* @keyframe animation-name */

@media screen and (min-width: 1000px) {
  body{
    background-color: white;
  }

  .pokemon-container{
    width: 1500px;
  }

  .main-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .card-container{
    width: 300px;
    height: 300px;
  }
  

  
}